@keyframes loader-anim {
  0% {
    left: -102px;
  }
  100% {
    left: 102px;
  }
}
@keyframes blink-text {
  0% {
    opacity: 1;
  }
  20% {
    opacity: 0.5;
  }
  40% {
    opacity: 0.2;
  }
  60% {
    opacity: 0.7;
  }
  80% {
    opacity: 0.1;
  }
  90% {
    opacity: 0.4;
  }
}
.preloader {
  display: block;
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background: $text-color;
  text-align: center;
  &__loading-title {
    font-size: 0.8rem;
    margin-top: 20px;
    animation: blink-text 1s infinite;
  }
  &img {
    animation: blink-text 1s infinite;
  }
}
.loading {
  overflow: hidden;
  position: absolute;
  top: 50%;
  margin-top: -100px;
  left: 50%;
  margin-left: -50px;
  width: 100px;
  height: 200px;
}
.progress {
  display: block;
  position: absolute;
  bottom: 50px;
  overflow: hidden;
  width: 100px;
  height: 1px;
  left: -102px;
  background-color: $brand-color;
  animation: loader-anim 1s 0s infinite cubic-bezier(0.785, 0.135, 0.15, 0.86);
}
